import React, { Component } from "react";
import { Grid, Paper, Tooltip, Link, Chip, Hidden } from "@material-ui/core";
import "../App.css";
import Typography from '@material-ui/core/Typography';
import { faTerminal, faDesktop, faLaptopCode, faMobile, faCode, faAlignLeft, faFingerprint, faArchive, faDatabase } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const projectData = require('../data.json');

export default class FeaturedProject extends Component {
    render() {
        return (
            projectData.projects.featured.map((item, i) => {
                var icon;
                if (item.category === "ML/AI") {
                    icon = <FontAwesomeIcon icon={faTerminal} style={{ color: "#8ec0ff" }} />;
                } else if (item.category === "Web Development") {
                    icon = <FontAwesomeIcon icon={faLaptopCode} style={{ color: "#8ec0ff" }} />;
                } else if (item.category === "Mobile Development") {
                    icon = <FontAwesomeIcon icon={faMobile} style={{ color: "#8ec0ff" }} />;
                } else if (item.category === "Data Science") {
                    icon = <FontAwesomeIcon icon={faDatabase} style={{ color: "#8ec0ff" }} />;
                } else if (item.category === "Cryptography") {
                    icon = <FontAwesomeIcon icon={faFingerprint} style={{ color: "#8ec0ff" }} />;
                } else {
                    icon = <FontAwesomeIcon icon={faArchive} style={{ color: "#8ec0ff" }} />;
                }
                return (
                    <Grid item xs={12} sm={12} md={4} lg={4} xl={4}>
                        <Paper elevation={5} style={{ padding: "20px", borderRadius: "5px" }}>
                            <Grid container direction="row">
                                <Typography variant="h5" style={{ color: "#8ec0ff" , marginBottom: "5px" }} >{item.project}</Typography>
                            </Grid>
                            <Typography paragraph >{item.description}</Typography>
                            <Chip variant="outlined" size="small" icon={icon} style={{ padding: "10px", marginBottom: "5px", marginRight: "5px" }} label={item.category} />
                            <Grid container direction="row">
                                {
                                    item.stack.map((stack, j) => {
                                        return (
                                            <Typography variant="subtitle1" style={{ color: "#8ec0ff", marginRight: "6px" }} > {stack} </Typography>
                                        );
                                    })
                                }
                            </Grid>
                            <Grid container direction="row">
                                <Tooltip title="Article" disableFocusListener style={{ marginRight: "10px" }}>
                                    <Link href={item.articlelink} target="_blank" color="inherit">
                                        <Grid container direction="row" alignItems="center">
                                            <FontAwesomeIcon icon={faAlignLeft} style={{ color: "white",  marginRight: "3px" }} />
                                            <Hidden mdDown> <Typography > Article </Typography> </Hidden>
                                        </Grid>
                                    </Link>
                                </Tooltip>
                                <Tooltip title="Code" disableFocusListener style={{ marginRight: "10px" }}>
                                    <Link href={item.codelink} target="_blank" color="inherit" direction="row">
                                        <Grid container direction="row" alignItems="center">
                                            <FontAwesomeIcon icon={faCode} style={{ color: "white" ,  marginRight: "3px"}} />
                                            <Hidden mdDown> <Typography > Code </Typography> </Hidden>
                                        </Grid>
                                    </Link>
                                </Tooltip>
                                <Tooltip title="Preview" disableFocusListener style={{ marginRight: "10px" }}>
                                    <Link href={item.preview} target="_blank" color="inherit" direction="row">
                                        <Grid container direction="row" alignItems="center">
                                            <FontAwesomeIcon icon={faDesktop} style={{ color: "white" , marginRight: "3px"}} />
                                            <Hidden mdDown> <Typography > Preview </Typography> </Hidden>
                                        </Grid>
                                    </Link>
                                </Tooltip>
                            </Grid>
                        </Paper>
                    </Grid>
                );
            })
        );
    }
};