import React, { Component } from "react";
import { Paper, Typography, Grid, Tooltip, Link} from "@material-ui/core";
import "../App.css";
import { faUserTie, faFileAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const expData = require('../data.json');

export default class ExpToasts extends Component {
    render() {
        return (
            expData.experience.map((item, i) => {
                return (
                    <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                        <Paper elevation={5} style={{ padding: "15px", marginBottom: "10px" }}>
                            <Typography variant="h6" style={{ color: "#8ec0ff" }} >{item.organization}</Typography>
                            <Typography variant="subtitle1" >{item.title}</Typography>
                            <Grid container direction="row" alignItems="flex-end">
                                <Grid item direction="row " xs={11} sm={11} md={11} lg={11} xl={11}>
                                    {
                                        item.positions.map((pos, j) => {
                                            return (
                                                <Grid container direction="row">
                                                    <Typography variant="subtitle1" style={{ marginRight: "5px" }}> <FontAwesomeIcon icon={faUserTie} style={{ color: "#8ec0ff" }} /> {pos.date} </Typography> <Typography variant="subtitle1" style={{ color: "#8ec0ff" }} > {pos.position}</Typography>
                                                </Grid>
                                            );
                                        })
                                    }
                                </Grid>
                                <Grid item direction="row " xs={1} sm={1} md={1} lg={1} xl={1}>
                                    <Tooltip title="Certificate" disableFocusListener >
                                        <Link color="inherit" href={item.link} target="_blank">
                                            <FontAwesomeIcon icon={faFileAlt} style={{ color: "white" }} />
                                        </Link>
                                    </Tooltip>
                                </Grid>
                            </Grid>
                        </Paper>
                    </Grid >
                );
            })
        );
    }
}

